/** Media Queries **/
.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.boton-seccion {
  background-color: #0da6f3;
  color: #FFFFFF;
  padding: 1.5rem 2rem;
  transition: background-color 300ms ease;
  font-weight: 700;
  width: 100%;
  border: none;
  text-transform: uppercase;
}
.boton-seccion:hover {
  cursor: pointer;
  background-color: #054362;
}

.btn-paginacion {
  width: fit-content;
}